import React from 'react';
import { NavLink } from 'react-router-dom';
import RouteContext from '../context/routeContext';

//header component
const Header = (props) => {
    return (
    <RouteContext.Consumer>
    {routeContext => {
    return (
        <div className="header">
            <h1 className="header__title">digi news</h1>

            <ul className="header__nav">
                <NavLink to="/" className="header__item" activeClassName="is-active" exact={true}>LATEST</NavLink>
                <NavLink to="/crypto" className="header__item" activeClassName="is-active">CRYPTO</NavLink>
                <NavLink to="/life" className="header__item" activeClassName="is-active">LIFE</NavLink>
                <NavLink to="/sports" className="header__item" activeClassName="is-active">SPORTS</NavLink>
                <NavLink to="/tech" className="header__item" activeClassName="is-active">TECH</NavLink>
            </ul>
            {
                routeContext.showMobileNav 
                    ? <button 
                        onClick={routeContext.handleMobileNav} 
                        className="material-icons waves-effect waves-pink header__nav--btn">close</button> 
                    : <button 
                        onClick={routeContext.handleMobileNav} 
                        className="material-icons waves-effect waves-pink header__nav--btn">clear_all</button> 
            }
            
        </div>
        )}
    }
    </RouteContext.Consumer>
    );
};

export default Header;


